  margin-left: calc(100% / 12 * 10 + 8px);
}
.grid-offset--11 {
  flex-basis: calc(100% / 12 * 1 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 11 + 8px);
}
.embedded-header {
  background-image: url("https://monosnap.com/image/HrfN4chtfzFNuk9VkmupiXatWIpMTE");
}
.embedded-figureWrap,
.embedded-rest {
  display: flex;
  flex-wrap: wrap;
  margin-right: -8px;
  margin-left: -8px;
}
.embedded-figureWrap > * {
  flex-basis: calc(100% / 12 * 6 - 16px);
  margin: 0 8px 16px;
  min-width: 250px;
  flex-grow: 1;
}
.embedded-rest > * {
  flex-basis: calc(100% / 12 * 4 - 16px);
  margin: 0 8px 16px;
  min-width: 250px;
  flex-grow: 1;
}
